@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* Colors */

:root {
    --error: #E55151;
    --brand-dark: #4071EE;
    --brand-light: #ABC3FF;
    --brand: #5786FF;
    --gray-5: #242E33;
    --gray-4: #64737A;
    --gray-3: #D1D9DE;
    --gray-2: #DFE5E8;
    --gray-1: #EDF1F2;
    --gray-0: #ffffff;

    --shadow-small: rgba(36, 46, 51, 0.1);
    --shadow-large: rgba(36, 46, 51, 0.3);
    --shadow-button: rgba(36, 46, 51, 0.2);
}
/* Typographic Scale: Major Third (1.250) */

/* Font Size Variables */
:root {
    --scale-factor: 1.250;
    --font-base: 14px;

    --scale-1: calc(var(--scale1) / var(--scale-factor));
    --scale1: 1em;
    --scale2: calc(var(--scale1) * var(--scale-factor));
    --scale3: calc(var(--scale2) * var(--scale-factor));
    --scale4: calc(var(--scale3) * var(--scale-factor));
    --scale5: calc(var(--scale4) * var(--scale-factor));
    --scale6: calc(var(--scale5) * var(--scale-factor));
}

body { font-family: 'Heebo', sans-serif; font-size: 14px; font-size: var(--font-base); }

/* Set default weight */
.text--small-body, .text--body, .text--big-body, .text--sub-heading, .text--heading, .text--sub-title, .text--title, .text--label {
    color: var(--gray-5);
    font-weight: 400;
}

/* Set line-heights */
.text--small-body, .text--body, .text--big-body {
    line-height: 1.6;
}
.text--sub-heading, .text--heading, .text--sub-title, .text--title , .text--label {
    line-height: 1.3;
}

/* Set font sizes */
.text--small-body, .text--label {
    font-size: calc(1em / 1.250);
    font-size: var(--scale-1);
}
.text--body { 
    font-size: 1em; 
    font-size: var(--scale1);
}
.text--big-body {
    font-size: calc(1em * 1.250);
    font-size: var(--scale2);
}
.text--sub-heading {
    font-size: calc(calc(1em * 1.250) * 1.250);
    font-size: var(--scale3);
}
.text--heading {
    font-size: calc(calc(calc(1em * 1.250) * 1.250) * 1.250);
    font-size: var(--scale4);
}
.text--sub-title {
    font-size: calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale5);
}
.text--title {
    font-size: calc(calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale6);
}

/* Visual Utilities */
.text--label {
    text-transform: uppercase;
    font-weight: 700;
}
.text--bold, strong {
    font-weight: 700;
}
.text--italic, i {
    font-style: italic;
}
.text--strike {
    text-decoration: line-through;
}
.text--underline {
    text-decoration: underline;
}

/* Alignment Utilities */
.text--align-center {
    text-align: center;
}
.text--align-right {
    text-align: right;
}
/* Colors */

:root {
    --error: #E55151;
    --brand-dark: #4071EE;
    --brand-light: #ABC3FF;
    --brand: #5786FF;
    --gray-5: #242E33;
    --gray-4: #64737A;
    --gray-3: #D1D9DE;
    --gray-2: #DFE5E8;
    --gray-1: #EDF1F2;
    --gray-0: #ffffff;

    --shadow-small: rgba(36, 46, 51, 0.1);
    --shadow-large: rgba(36, 46, 51, 0.3);
    --shadow-button: rgba(36, 46, 51, 0.2);
}
body {
  background: var(--gray-1);
}
.ctnr {
  padding: 100px;
}

/* Spacing Units */

:root {
    --su-5: 5px;
    --su-10: 10px;
    --su-20: 20px;
    --su-30: 30px;
    --su-50: 50px;
    --su-100: 100px;
}
/* TopNav Component */

.topNav {
    background: var(--gray-0);
    box-shadow: 0px 2px 4px var(--shadow-small), 0px 8px 16px var(--shadow-small);
    padding: var(--su-20) var(--su-50);
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
}
.topNav__menu {
    justify-content: right;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, -webkit-max-content));
    grid-template-columns: repeat(auto-fit, minmax(0, max-content));
    grid-gap: var(--su-10);
}
.topNav__menu__item {
    color: var(--gray-5);
    padding: 12px;
    text-decoration: none;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.topNav__menu__item:hover {
    background: var(--gray-1);
}
.topNav__menu__item__active {
    color: var(--brand);
    background: var(--gray-1);
}
/* Container Component */

/* container Settings - Affects layout */
.container {
    display: grid;
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
}

/* Container Sizes - Affects padding */
.container--small {
    padding: var(--su-10);
}
.container--medium {
    padding: var(--su-30);
}
.container--large {
    padding: var(--su-50);
}
/* container Sizes - Affects spacing between children elements */
.container--spacing-10 {
    grid-gap: var(--su-10);
}
.container--spacing-20 {
    grid-gap: var(--su-20);
}
.container--spacing-30 {
    grid-gap: var(--su-30);
}
.container--spacing-50 {
    grid-gap: var(--su-10);
}
.container--spacing-100 {
    grid-gap: var(--su-100);
}

/* Container Layout - Affects number of columns */
.container--col-2 {
    grid-template-columns: 1fr 1fr;
}
.container--col-3 {
    grid-template-columns: 1fr 1fr 1fr;
}
.container--col-4 {
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.container--col-auto {
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
}

/* Container Utilities */
.container--limit-height {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}
.container--no-top-padding{
    padding-top: 0;
}
/* Card Component */

/* Card Settings - Affects layout */
.card {
    border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    -webkit-border-radius: 6px;
    overflow: hidden;
}

/* Card Types - Affects look and feel */
.card--box, .card--modal {
    background: var(--gray-0);
    box-shadow: 0px 2px 4px var(--shadow-small), 0px 8px 16px var(--shadow-small);
}
.card--modal {
    box-shadow: 0px 8px 16px var(--shadow-large);
}
.card--border {
    background: transparent;
    border: 1px solid var(--gray-3);
}

/* Card Elements */
.card__header { 
    border-bottom: 1px solid var(--gray-3);
    padding-top: 15px;
    padding-bottom: 15px; 
}
.card__header--small {
    padding-left: 10px;
    padding-right: 10px;
}
.card__header--medium {
    padding-left: 30px;
    padding-right: 30px;
}
.card__header--large {
    padding-left: 50px;
    padding-right: 50px;
}
/* Button Component */

/* Button Settings - Affects layout */
.button {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    justify-content: center;
    grid-gap: 5px;
    line-height: 16px;
    border: 0;
    outline: 0;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    -webkit-border-radius: 3px;
    transition: background-color 0.2s ease-in, border-color 0.2s ease-in;
    -webkit-transition: background-color 0.2s ease-in, border-color 0.2s ease-in;
    -moz-transition: background-color 0.2s ease-in, border-color 0.2s ease-in;
    -ms-transition: background-color 0.2s ease-in, border-color 0.2s ease-in;
    -o-transition: background-color 0.2s ease-in, border-color 0.2s ease-in;
}
.button:hover {
    cursor: pointer;
}

/* Button Types - Affects look and feel */
.button--primary {
    background: var(--brand);
    color: var(--gray-0);
    box-shadow: 0px 2px 4px var(--shadow-small), inset 0px -2px 2px var(--shadow-button), inset 0px -4px 8px var(--shadow-small);
    font-weight: 700;
}
.button--primary:hover {
    background: var(--brand-dark);
}

.button--secondary {
    background: transparent;
    color: var(--gray-5);
    border: 1px solid var(--gray-3);
}
.button--secondary:hover {
    border-color: var(--brand);
}
.button--link {
    background: transparent;
    padding: 0;
    font-weight: 700;
    color: var(--brand);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.button--link:hover {
    color: var(--brand-dark);
}
.button--link.button--small, .button--link.button--medium, .button--link.button.large {
    padding: 0;
}
.button--primary:disabled, .button--secondary:disabled {
    background: var(--gray-2);
    color: var(--gray-4);
    box-shadow: 0px 2px 4px var(--shadow-small), inset 0px -2px 2px var(--shadow-button), inset 0px -4px 8px var(--shadow-small);
}

/* Button Sizes - Affects padding and font size */
.button--small {
    padding: 7px;
    font-size: var(--scale-1);
}
.button--medium {
    padding: 12px;
    font-size: var(--scale1);
}
.button--large {
    padding: 17px;
    font-size: var(--scale2);
}
/* Image Component */

.image {
    width: 100%;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
/* Form Components */

.form-field {
    display: grid;
    grid-gap: var(--su-5);
}
.input {
    font-family: 'Heebo', sans-serif;
    font-size: var(--scale1);
    line-height: 16px;
    color: var(--gray-5);
    background: var(--gray-0);
    border: 1px solid var(--gray-3);
    outline: 0;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
.input:hover, .input:focus {
    border-color: var(--brand);
}
.input:focus {
    box-shadow: 0 0 10px var(--brand-light);
}
.input:disabled {
    color: var(--gray-4);
    background: var(--gray-1);
    border-color: var(--gray-2);
    cursor: not-allowed;
}
.input.has-error {
    border-color: var(--error);
}
.input::-webkit-input-placeholder {
    color: var(--gray-4);
}
.input:-ms-input-placeholder {
    color: var(--gray-4);
}
.input::-ms-input-placeholder {
    color: var(--gray-4);
}
.input::placeholder {
    color: var(--gray-4);
}

/* Input Sizes - Affects padding and font size */
.input--small {
    padding: 6px;
    font-size: var(--scale-1);
}
.input--medium {
    padding: 11px;
    font-size: var(--scale1);
}
.input--large {
    padding: 16px;
    font-size: var(--scale2);
}

/* Select Styling */
.select {
    position: relative;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 1L5 5L1 1' stroke='%23002148' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: calc(100% - 10px) center;
}
.select:hover {
    cursor: pointer;
}
.select:disabled {
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 1L5 5L1 1' stroke='%23002148' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: calc(100% - 10px) center;
    cursor: not-allowed;
}
